* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#map {
    height: 100vh;
    /* width: 100%; */
  }
  html, body {
    height: 100%;
    margin: 0;
    padding: 0;
  }


  /* list component container */
  /* .App {
    width: 100%;

  } */
  .list-container, .map-container  {
    height: 100vh;
    float: left;

  }
  .map-container {
    width: 75%;
    float: left;
  }

  .list-container {
    width: 25%;   
    overflow: scroll;
    overflow-x: hidden;
    padding: 20px;
  }

  .list-container h2 {
    text-align: center;
  }
  #search-input {
    padding: 10px 15px;
    font-size: 18px;
    width: 100%;
    margin-bottom: 30px
  }


  .location-item  {
    padding: 20px;
    margin: 0;
    cursor: pointer;
    border-top: 1px solid #eee;
    color: #888
  }

  .location-item:hover {
    background-color: #ddd
  }
  .location-item i {
    color: #777;
    margin-right: 5px;
  }
  /* .locations-list { */
    /* margin-left: -20px;
    margin-right: -20px;
  } */


  .venue-info h2 {
    color: #888;
  }


  .venue-loc, .venue-cat  {
    color: #888
  }
  .venue-loc i, .venue-cat i {
    color: #777;
    margin-right: 5px;
  }

  #hamburger-menu {
    display: none;
  }

  
  @media all and (max-width: 991px) {
    #hamburger-menu {
      display: block;
      position: absolute;
      right: 20px;
      top: 20px;
      z-index: 10000;
      width: 70px;
      height: 70px;
      background-color: #fa3e3e;
      padding: 10px;
      border-radius: 50%;
      cursor: pointer;
      text-align: center;
    }
  
    #hamburger-menu i {
      color: #fff;
      line-height: 2.4;
    }

    .map-container {
      width: 100%;
    }
    .list-container {
      position: fixed;
      width: 300px;
      left: -100%;
      -webkit-transition: all 500ms ease-in-out;
      -o-transition: all 500ms ease-in-out;
      transition: all 500ms ease-in-out;
      z-index: 99;
      background-color: #fff;
  }
  .list-container .locations-list p, .list-container #search-input, .list-container h2 {
    display: none;
  }

  .list-container.toggle {
      left: 0;
  }
  .list-container.toggle .locations-list p, .list-container.toggle #search-input, .list-container.toggle h2 {
    display: block;
  }
} 


.err-container { 
	padding: 30px;
	padding-top: 120px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.err-container h1:first-child {
	text-align: center;
}

.err-container h1, .err-container p {
	color: #777;
}

.err-container h1 i {
	color: orange;
}



